import React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import styled from "styled-components"

import Layout from "../components/layout"
import SEO from "../components/seo"

const ProjectsList = styled.div`
  h2 {
    text-align: center;
  }

  ul {
    padding: 0;
    list-style: none;
    max-width: 600px;
    margin: 0 auto;

    li {
      h3 {
        margin-bottom: 0;
        color: var(--main-color-darker);
      }

      a {
        display: flex;
        flex-direction: column;
        padding: 1rem 1.5rem;
        border-radius: 10px;
        box-shadow: -2px 3px var(--main-color-darker);
        margin-bottom: 2rem;
        background-color: white;
        transition: transform 0.3s ease;

        &:hover {
          text-decoration: none;
          transform: translateY(-0.25rem);
        }
      }

      :before {
        content: initial;
      }
    }
  }
`

export default function IndexPage({ data }) {
  return (
    <Layout dimmed>
      <SEO title="Home" />
      <ProjectsList>
        <h2>Projects</h2>

        <ul>
          {data.strapi.projects.map(project => (
            <li key={project.UID}>
              <Link to={`/projects/${project.UID}`}>
                <h3>{project.Title.split("-").slice(1).join("-").trim()}</h3>
                <div>
                  {project.Description.replace(/(<([^>]+)>)/gi, "").slice(
                    0,
                    120
                  )}
                  ...
                </div>
              </Link>
            </li>
          ))}
        </ul>
      </ProjectsList>
    </Layout>
  )
}

export const query = graphql`
  {
    strapi {
      guides(limit: 1000) {
        UID
        Title
      }
      projects(limit: 1000) {
        UID
        Title
        Description
      }

      pageCategories {
        UID
        Name
        pages {
          UID
          Title
          featuredImage {
            previewUrl
            url
          }
        }
      }
    }
  }
`
